import React, { Suspense } from "react";
import { Route, withRouter, Switch, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import AppLayout from "../../layout/AppLayout";

const Dashboard = React.lazy(() =>
  import(/* webpackChunkName: "viwes-dashboard" */ "./dashboard")
);
const Notebook = React.lazy(() =>
  import(/* webpackChunkName: "viwes-notebook" */ "./notebook")
);
const HyperParameter = React.lazy(() =>
  import(/* webpackChunkName: "viwes-hyperparameter" */ "./hyperparameter")
);
const Pipelines = React.lazy(() =>
  import(/* webpackChunkName: "viwes-pipelines" */ "./pipelines")
);
// const Experiments = React.lazy(() =>
//   import(/* webpackChunkName: "viwes-experiments" */ "./experiments")
// );
const Models = React.lazy(() =>
  import(/* webpackChunkName: "viwes-models" */ "./models")
);
const Filemanager = React.lazy(() =>
  import(/* webpackChunkName: "viwes-filemanager" */ "./filemanager")
);
const Connectors = React.lazy(() =>
  import(/* webpackChunkName: "viwes-connectors" */ "./connectors")
);

const Application = React.lazy(() => import("./application"));

const App = ({ match }) => {
  return (
    <AppLayout>
      <div className="dashboard-wrapper">
        <Suspense fallback={<div className="loading" />}>
          <Switch>
            <Redirect
              exact
              from={`${match.url}/`}
              to={`${match.url}/dashboard`}
            />
            <Route
              path={`${match.url}/dashboard`}
              render={(props) => <Dashboard {...props} />}
            />
            <Route
              path={`${match.url}/workspace`}
              render={(props) => <Notebook {...props} />}
            />
            <Route
              path={`${match.url}/connectors`}
              render={(props) => <Connectors {...props} />}
            />
            <Route
              path={`${match.url}/filemanager`}
              render={(props) => <Filemanager {...props} />}
            />
            <Route
              path={`${match.url}/hyperparameter`}
              render={(props) => <HyperParameter {...props} />}
            />
            <Route
              path={`${match.url}/pipelines`}
              render={(props) => <Pipelines {...props} />}
            />
            {/* <Route
              path={`${match.url}/experiments`}
              render={(props) => <Experiments {...props} />}
            /> */}
            <Route
              path={`${match.url}/models`}
              render={(props) => <Models {...props} />}
            />
            <Route
              path={`${match.url}/apps`}
              render={(props) => <Application {...props} />}
            />
            <Redirect to="/error" />
          </Switch>
        </Suspense>
      </div>
    </AppLayout>
  );
};

const mapStateToProps = ({ menu }) => {
  const { containerClassnames } = menu;
  return { containerClassnames };
};

export default withRouter(connect(mapStateToProps, {})(App));
